* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

:root {
  --unpassable-block: rgb(104, 104, 104);
  --unpassable-block-top: rgb(235, 186, 186);
  --block: #cecece;
}

.center {
  width: max-content;
  height: max-content;
  margin: auto 0;
  display: flex;
  gap: 83px;
}

main {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: #dfdfdf;
  display: flex;
  justify-content: center;
  align-items: center;
}

.left {
  display: flex;
  justify-content: center;
}

.right {
  color: black;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.code {
  background: #cacaca;
  border-radius: 24px;
  width: 396px;
  height: 673px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  overflow: hidden;
}

.code > h1 {
  font-size: 36px;
  color: white;
  font-weight: bold;
  padding: 38px 0 0 30px;
  text-align: left;
  width: 100%;
}

.code > textarea {
  color: white;
  border: none;
  background-color: #b1b1b1;
  border-radius: 24px;
  height: 460px;
  width: 320px;
  padding: 30px 20px;
  display: flex;
  align-items: flex-end;
  /* font-family: Menlo, Monaco, "Courier New", monospace; */
  resize: none;
}

.code > textarea:focus {
  outline: none;
}

.code > button {
  border: none;
  width: 100%;
  height: 80px;
  background: linear-gradient(87.04deg, #70ecdd -1.92%, #4ddea1 84.42%);
  color: white;
  font-size: 28px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
  opacity: 0.9;
}

.code > button:hover {
  opacity: 1;
  font-size: 31px;
}

.controls {
  width: 810px;
  height: 100px;
  display: flex;
  gap: 42px;
  margin-bottom: 30px;
}

.canvas {
  background-color: #cacaca;
  border-radius: 24px;
  width: 810px;
  height: 530px;
  overflow: hidden;
  position: relative;
}

.sky {
  width: 100%;
  height: 100%;
  background: linear-gradient(
    0.3deg,
    rgba(92, 196, 255, 0.46) 20.35%,
    #a9fffb 112.32%
  );
}

.grass {
  position: absolute;
  top: 30%;
  width: 120%;
  height: 70%;
  transform: skewX(-20deg);
  background: linear-gradient(
    200.27deg,
    #6bffa6 35.24%,
    rgba(33, 231, 159, 0.69) 76.18%
  );
}

.ground {
  position: absolute;
  width: 93%;
  height: 70%;
  top: 15%;
  left: 3%;
  background: linear-gradient(212.37deg, #c4c4c4 28.97%, #b0b0b0 51.63%);
  transform: skew(-20deg);
  border-radius: 24px;
  padding: 3%;
  display: grid;
  grid-template-columns: repeat(10, 1fr);
}

.ground > div {
  background-color: var(--block);
  width: 59px;
  height: 54px;
  border-radius: 12px;
  transition: all 0.3s;
  position: relative;
  display: flex;
  padding: 10px;
  font-size: 0.75em;
  color: rgb(100, 100, 100);
  font-weight: bold;
}

.ground > .house > img {
  width: 60px;
  transform: skew(20deg);
  position: absolute;
  left: -10px;
  bottom: 0px;
  filter: drop-shadow(10px -5px 5px #acacac);
}

.ground > .lake > img {
  width: 67px;
  transform: skew(20deg);
  position: absolute;
  left: -10px;
  bottom: 10px;
}

.ground > .character > img {
  width: 50px;
  transform: skew(20deg);
  position: absolute;
  left: -3px;
  bottom: 10px;
}

@keyframes floatGoal {
  0%,
  100% {
    transform: translateY(0px) translateX(0px);
  }

  50% {
    transform: translateY(-2px) translateX(-2px);
    box-shadow: 5px 4px 5px rgba(0, 0, 0, 0.25);
  }
}

.goal {
  background: linear-gradient(306.93deg, #53ecab 5.91%, #59ffcd 108.66%);
  animation-duration: 2.2s;
  animation-name: floatGoal;
  animation-direction: alternate;
  animation-timing-function: ease-in-out;
  animation-iteration-count: 1;
}

.not {
  background-color: #8d8d8d !important;
}

.unpassable {
  background-color: var(--unpassable-block-top) !important;
  box-shadow: 1px 1px var(--unpassable-block), 2px 2px var(--unpassable-block),
    3px 3px var(--unpassable-block), 4px 4px var(--unpassable-block),
    5px 5px var(--unpassable-block), 6px 6px var(--unpassable-block),
    7px 7px var(--unpassable-block), 8px 8px var(--unpassable-block),
    8px 8px var(--unpassable-block), 9px 9px var(--unpassable-block),
    10px 10px var(--unpassable-block);
  transform: translateX(0px) translateY(-10px);
}

.tesla {
  background-color: white;
  width: 30px;
  height: 30px;
  position: absolute;
  border-radius: 50%;
  transition: all 500ms ease;
  z-index: 10;
  text-align: center;
}

.tesla:after {
  position: absolute;
  transform: translateY(-100%) translateX(-60%);
  content: "";
  z-index: 3;
  width: 0;
  height: 0;
  border-left: 0 solid transparent;
  border-right: 0 solid transparent;
  border-top: 0 solid rgba(52, 255, 245, 0.46);
  transition: all 500ms ease;
}

@keyframes scanning {
  0%,
  100% {
    transform: translateY(-100%) translateX(-60%) rotate3d(1, 1, 0, 0deg)
      skew(0deg);
  }

  50% {
    transform: translateY(-100%) translateX(-60%) rotate3d(1, 1, 0, 40deg)
      skew(0deg);
  }
}

.tesla.scan:after {
  position: absolute;
  transform-origin: 100% 120%;
  transform: translateY(-100%) translateX(-60%);
  content: "";
  z-index: 3;
  width: 0;
  height: 0;
  border-left: 15px solid transparent;
  border-right: 15px solid transparent;
  border-top: 75px solid rgba(52, 255, 245, 0.46);
  transition: all 500ms ease;

  animation-name: scanning;
  animation-duration: 2s;
  animation-iteration-count: 1;
  animation-direction: both;
  animation-timing-function: ease-in-out;
}

.tesla.scan.red:after {
  border-top: 75px solid rgba(243, 70, 122, 0.46);
}

.tesla.scan.green:after {
  border-top: 75px solid rgba(25, 255, 140, 0.589);
}

