  background-color: linear-gradient(306.93deg, #53ecab 5.91%, #59ffcd 108.66%);
  background-color: #53ecab;
} */

.controls > div {
  width: 100px;
  height: 100px;
  background-color: #cacaca;
  border-radius: 24px;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* .controls > .left,
.controls > .right {
  transform: rotate(-90deg);
} */

.controls > div:hover {
  transform: scale(1.02);
}

.controls > div:active {
  background-color: #adadad;
}

.controls img {
  width: 80%;
}

.controls > .left > img {
  transform: rotate(90deg);
}

.controls > .right > img {
  transform: rotate(-90deg);
}

@keyframes shake {
  10%,
  90% {
    transform: translateX(-1px);
  }

  20%,
  80% {
    transform: translateX(2px);
  }

  30%,
  50%,
  70% {
    transform: translateX(-4px);
  }

  40%,
  60% {
    transform: translateX(4px);
  }
}

.controls > .shake {
  background-color: rgb(224, 129, 129);
  color: white;
  animation: shake 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
  transform: translate3d(0, 0, 0);
  backface-visibility: hidden;
  perspective: 1000px;
}

.canvas > .modal {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--block);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  transition: transform 1s ease;
  z-index: 4;
  padding: 80px;
}

.canvas > .modal > h1 {
  font-size: 36px;
  color: white;
  font-weight: bold;
  text-align: center;
  width: 80%;
  padding-bottom: 30px;
  border-bottom: #c4c4c4 1px solid;
}

.canvas > .modal > p {
  padding-top: 30px;
  text-align: center;
  color: rgb(104, 104, 104);
}

.canvas > .lightning {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: #b8b7b7;
  position: absolute;
  right: 20px;
  bottom: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.canvas > .lightning > img {
  width: 80%;
}

.canvas > .restart {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: #b8b7b7;
  position: absolute;
  left: 20px;
  top: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.canvas > .restart > img {
  width: 60%;
}

.arrow {
  width: 50px;
  height: 50px;
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all ease 0.3s;
}

.arrow:hover {
  filter: drop-shadow(0 0 5px rgb(170, 170, 170));
  transform: scale(1.04);
}

.arrow > img {
  width: 65%;
}

.arrow.left {
  left: 10px;
  top: 50%;
  transform: rotate(-180deg);
}

.arrow.right {
  right: 10px;
  top: 50%;
}
